<template>
    <tree-list-layout>

        <div slot="title">
            <ui-icon-button @signal_Father="back"></ui-icon-button>
        </div>

        <div slot="content" class="fs-14">
            
            <ui-page-header>{{ $store.state.user.display_name }}</ui-page-header>

            <v-card class="mx-1 pa-4 mt-3 px-5">
                
                <!-- -->
                <ui-content-title>
                    <div class="fs-14">Bonus Statement</div>
                </ui-content-title>

                <user-center-msg></user-center-msg>

                <!-- -->
                <ui-content-title>
                    <div class="fs-14 pt-5">
                        Bonus Eligibility:  <span class="text-success">Active</span>
                    </div>
                </ui-content-title>

                <user-center-order></user-center-order>

                <!-- -->
                <ui-content-title>
                    <div class="fs-14 pt-5">
                        本周期支付级别:  <span class="text-pv">
                            <vip-label-simpler :item="$store.state.bonus.vip"></vip-label-simpler>
                        </span>
                    </div>
                </ui-content-title>

                <user-center-bonus></user-center-bonus>
            </v-card>
        </div>
    </tree-list-layout>
</template>

<script>
import TreeListLayout from '@/components/Layout/TreeListLayout.vue'
import UserBuys from './UserBuys.vue'
import UiPageHeader from '@/components/Ui/UiPageHeader.vue'
import UiContentPanel from '@/components/Ui/UiContentPanel.vue'
import UiContentTitle from '@/components/Ui/UiContentTitle.vue'
import UserCenterMsg from './Inner/UserCenterMsg.vue'
import UiIconButton from '../../../components/Ui/UiIconButton.vue'
import UserCenterOrder from './Inner/UserCenterOrder.vue'
import VipLabel from '../../../components/Tree/VipLabel.vue'
import VipLabelSimpler from '../../../components/Tree/VipLabelSimpler.vue'
import UserCenterBonus from './Inner/UserCenterBonus.vue'

    export default {
        components: { TreeListLayout, UserBuys, UiPageHeader, UiContentPanel, UiContentTitle, UserCenterMsg, UiIconButton, UserCenterOrder,
                VipLabel,
                VipLabelSimpler,
                UserCenterBonus },
        name: '',
        data() {
            return {
                userss: null
            }
        },
        mounted() {
            console.log(this.$store.state.bonus)
        },
        methods: {

            back() {
                this.$router.push({
                    path: '/home/account' })
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


