<template>
    <div>

        <ui-black-line-table>
            <thead slot="header">
                <tr>
                    <td>Individual Credit</td>
                    <td>Organization Credit</td>
                    <td>直推 Active 下线</td>
                </tr>
            </thead>

            <tbody slot="body">
                <tr>
                    <td>
                        {{ user_s.ipv }}
                    </td>
                    <td>
                        {{ user_s.son_pv }}
                    </td>
                    <td>
                        {{ user_s.son_can_num }}
                    </td>
                </tr>
            </tbody>
        </ui-black-line-table>

    </div>
</template>

<script>
import UiBlackLineTable from '../../../../components/Ui/UiBlackLineTable.vue'
    export default {
  components: { UiBlackLineTable },
        name: '',
        data() {
            return {
                user_s: null
            }
        },
        mounted() {
        
            let users = this.$store.state.users_sponser
            if (users) {
                users.map(e => {
                    if (e.id == this.$store.state.user.id) {
                        this.user_s = e
                    }
                })
            }
        },
        computed: {
            pv () {
            }
        },
        methods: {
            
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


