<template>
    <table class="table-black-line" :style="{ 'width': w }">
        <slot name="header">

        </slot>

        <slot name="body">

        </slot>
    </table>
</template>

<script>
    export default {
        props: {
            w: {
                type: String,
                default: '64%'
            }
        },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
.table-black-line,
.table-black-line td
    border: 1px solid #979798 !important

.table-black-line td
    padding-left: 6px
</style>


