<template>
    <v-row>
        <v-col cols="6">
            <ui-closed-text>
                <div slot="left" class="sub-header fs-14"> Member ID:</div>
                <div slot="right" class="pl-3">
                    {{ $store.state.user.number_code }}
                </div>
            </ui-closed-text>
            <br/>
            <ui-closed-text>
                <div slot="left" class="sub-header fs-14"> Member Name:</div>
                <div slot="right" class="pl-3">
                    {{ $store.state.user.username }}
                </div>
            </ui-closed-text>
        </v-col>
        <v-col cols="6" class="text-right">
            <div>
                {{ view.getTodayEn() }}
            </div>
            
            <div class="pl-3">
                Contents Period: 由&nbsp;{{ view.ser_timed($store.state.bonus.chronu.start, true) }}至&nbsp;{{ view.ser_timed($store.state.bonus.chronu.end, true) }}
            </div>
        </v-col>
    </v-row>
</template>

<script>
import UiClosedText from '../../../../components/Ui/UiClosedText.vue'
    export default {
  components: { UiClosedText },
        name: '',
        data() {
            return {
                
            }
        }
    }
</script>

<style lang="sass" scoped>
    
</style>


